import React, { useEffect, useState } from "react";
import { supabase } from "../database/supabaseClient";

const INITIAL_START_DATE = "2025-11-17 07:18:00+07";

const DashboardFrame: React.FC = () => {
  const [signupStats, setSignupStats] = useState<any>(null);
  const [revenueStats, setRevenueStats] = useState<any>(null);
  const [recentPayments, setRecentPayments] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);


    useEffect(() => {
    async function load() {
      // Call the RPC that runs the SQL we just wrote
      const { data, error } = await supabase.rpc("revenue_stats");
      console.log(data)
      if (error) {
        console.error("Error fetching revenue stats:", error);
      } else {
        setRevenueStats(data);
      }
      setLoading(false);
    }

    load();
  }, []);


  useEffect(() => {
    async function load() {
      // 1. Signups
      const { data: sData } = await supabase.rpc("signup_stats", {
        tz: "Asia/Ho_Chi_Minh",
        exclude_pattern: null,
        min_created_at: INITIAL_START_DATE,
      });

      const extracted =
        Array.isArray(sData) && sData.length > 0
          ? sData[0]?.signup_stats ?? null
          : sData;
      setSignupStats(extracted);

      // 2. Revenue
      const { data: rData } = await supabase.rpc("revenue_stats", {});
      setRevenueStats(rData);

      // 3. Last 10 Payments (cleaned)
      const { data: pData, error: pErr } = await supabase
        .from("event_with_email")
        .select("*")
        .eq("paid", true)
        .order("created_at", { ascending: false })
        .limit(10);

      if (!pErr && pData) {
        const formatted = pData.map((row) => {
          const vnTime = new Intl.DateTimeFormat("vi-VN", {
            dateStyle: "short",
            timeStyle: "medium",
          }).format(new Date(row.created_at));

          return {
            id: row.id,
            price: row.price,
            note: row.note,
            created_at: row.created_at,
            created_at_vn: vnTime,
            email: row.email ?? "Unknown",
          };
        });

        setRecentPayments(formatted);
      }

      setLoading(false);
    }

    load();
  }, []);

  const gridStyle: React.CSSProperties = {
    display: "grid",
    gridTemplateColumns: "repeat(auto-fit, minmax(140px, 1fr))",
    gap: "16px",
    marginTop: "30px",
  };


function mapType(code: string) {
  const map: Record<string, string> = {
    TYPE_I: "AI Image",
    TYPE_II: "AI Video",
    TYPE_III: "AI Image & Video",
  };
  return map[code] || code;
}


  return (
    <div style={{ padding: "40px" }}>
      <h2 style={{ marginBottom: "20px" }}>Dashboard</h2>

      {loading && <p>Loading‚Ä¶</p>}

      {!loading && (
        <>
          <br/> <br/> <br/> 
          <h3 style={{ marginTop: "40px", marginBottom: "10px" }}>
                Signups
              </h3>

          {/* SIGNUP CARDS */}
          {signupStats && (
            <div style={gridStyle}>
              <StatCard label="Today" value={signupStats.today} />
              <StatCard label="Yesterday" value={signupStats.yesterday} />
              <StatCard label="This Week" value={signupStats.this_week} />
              <StatCard label="Last Week" value={signupStats.last_week} />
              <StatCard label="This Month" value={signupStats.this_month} />
              <StatCard label="Last Month" value={signupStats.last_month} />
            </div>
          )}

          {/* REVENUE */}
          {revenueStats && (
            <>
              <h3 style={{ marginTop: "40px", marginBottom: "10px" }}>
                Revenue
              </h3>

              <div style={gridStyle}>
                <StatCard
                  label="Total Revenue (VND)"
                  value={formatMoney(revenueStats.total_revenue)}
                />

                {Object.entries(revenueStats.revenue_by_type || {}).map(
                  ([type, amount]) => (
                    <StatCard
                      key={`rev-${type}`}
                      label={`Revenue ${type}`}
                      value={formatMoney(amount)}
                    />
                  )
                )}

                {Object.entries(revenueStats.count_by_type || {}).map(
                  ([type, count]) => (
                    <StatCard
                      key={`cnt-${type}`}
                      label={`Count ${type}`}
                      value={count}
                    />
                  )
                )}
              </div>
            </>
          )}

          {/* LAST 10 PAYMENTS */}
          <h3 style={{ marginTop: "50px", marginBottom: "10px" }}>
            Last 10 Payments
          </h3>

          <table
            style={{
              width: "100%",
              borderCollapse: "collapse",
              marginTop: "10px",
              background: "#fff",
              borderRadius: "10px",
              overflow: "hidden",
            }}
          >
            <thead style={{ background: "var(--border)" }}>
              <tr>
                <th style={th}>Email</th>
                <th style={th}>Time (VN)</th>
                <th style={th}>Price</th>
                <th style={th}>Ticket Type</th>
              </tr>
            </thead>
            <tbody>
              {recentPayments.map((p) => (
                <tr key={p.id}>
                  <td style={td}>{p.email}</td>
                  <td style={td}>{p.created_at_vn}</td>
                  <td style={td}>{formatMoney(Number(p.price))}</td>
                  <td style={td}>{mapType(p.note)}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </>
      )}
    </div>
  );
};

// Styles
const th: React.CSSProperties = {
  padding: "10px",
  fontWeight: "600",
  fontSize: "14px",
  textAlign: "left",
  borderBottom: "1px solid var(--border)",
};

const td: React.CSSProperties = {
  padding: "10px",
  fontSize: "14px",
  borderBottom: "1px solid #eee",
};

// Money formatter
function formatMoney(n: number) {
  if (n == null) return "0";
  return n.toLocaleString("vi-VN");
}

const StatCard = ({ label, value }: any) => (
  <div
    style={{
      padding: "20px",
      background: "#fff",
      border: "1px solid var(--border)",
      borderRadius: "10px",
      boxShadow: "0 1px 4px rgba(0,0,0,0.08)",
      display: "flex",
      flexDirection: "column",
      justifyContent: "center",
      alignItems: "center",
    }}
  >
    <span style={{ opacity: 0.7, fontSize: "14px", marginBottom: "6px" }}>
      {label}
    </span>
    <strong style={{ fontSize: "26px" }}>{value}</strong>
  </div>
);

export default DashboardFrame;
