import React, { useState } from "react";

import { getQstyle } from "./mini-studio/qstyle";
import CmsSideNav from "./CmsSideNav";
import CmsHeader from "./CmsHeader";

type Props = {
  title?: string;
  onThemeChange?: (theme: "light" | "dark") => void;
  children: React.ReactNode | ((ctx: { query: string }) => React.ReactNode);
};

export default function MiniStudioShell({
  title = "Tinh Hoa Vi·ªát CMS",
  onThemeChange,
  children,
}: Props) {
  const [theme, setTheme] = useState<"light" | "dark">("light");
  const [navOpen, setNavOpen] = useState(false);

  const [query, setQuery] = useState("");

  return (
    <div className={`app ${theme}`}>
      {/* ‚úÖ THIS IS WHAT YOU LOST */}
      <style>{getQstyle(navOpen)}</style>

      <CmsSideNav navOpen={navOpen} setNavOpen={setNavOpen} />

      <CmsHeader
        title={title}
        theme={theme}
        setTheme={setTheme}
        navOpen={navOpen}
        setNavOpen={setNavOpen}
        onThemeChange={onThemeChange}
      />

      <main>
        {typeof children === "function" ? children({ query }) : children}
      </main>

      <footer>Tinh Hoa Viet</footer>
    </div>
  );
}
