import React, { useEffect, useRef, useState } from "react";
import { Settings, LogOut, Sun, Moon, Menu, X } from "lucide-react";

export default function CmsHeader({
  title = "Tinh Hoa Vi·ªát CMS",
  onThemeChange,
  theme,
  setTheme,
  navOpen,
  setNavOpen,
}: {
  title?: string;
  onThemeChange?: (theme: "light" | "dark") => void;
  theme: "light" | "dark";
  setTheme: (t: "light" | "dark") => void;
  navOpen: boolean;
  setNavOpen: (v: boolean) => void;
}) {

  const [open, setOpen] = useState(false);
  const menuRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleClick = (e: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(e.target as Node)) {
        setOpen(false);
      }
    };
    document.addEventListener("click", handleClick);
    return () => document.removeEventListener("click", handleClick);
  }, []);

  useEffect(() => {
    document.body.dataset.theme = theme;
    onThemeChange?.(theme);
  }, [theme, onThemeChange]);

  return (
    <>
      <style>{`
        header {
          background: var(--panel); border-bottom: 1px solid var(--border);
          padding: 12px 20px; display: flex; align-items: center;
          justify-content: space-between; position: sticky; top: 0; z-index: 30;
        }
        .brand { display: flex; align-items: center; gap: 8px;
          font-weight: 600; color: var(--text); font-size: 14px;}
        .brand-icon { width: 24px; height: 24px;
          background: linear-gradient(135deg, #10b981, #059669);
          border-radius: 3px;}
        .actions { display: flex; align-items: center; gap: 10px;}
        .dropdown { position: relative; }
        .menu { position: absolute; top: 110%; right: 0; background: var(--panel);
          border: 1px solid var(--border); border-radius: 8px;
          box-shadow: 0 4px 10px rgba(0,0,0,0.2);
          width: 160px; overflow: hidden; z-index: 50; display: none;}
        .menu.open { display: block; }
        .menu button { width: 100%; justify-content: flex-start;
          background: none; border-radius: 0; }
        .menu button:hover { background: var(--accent-bg); }
        .menu hr { border: none; border-top: 1px solid var(--border); margin: 4px 0; }
      `}</style>

      <header>
        <div className="brand">
          <button
            onClick={() => setNavOpen(!navOpen)}
            style={{ background: "none", border: "none", cursor: "pointer" }}
          >
            {navOpen ? <X size={18} /> : <Menu size={18} />}
          </button>

          <div className="brand-icon" />
          {title}
        </div>

        <div className="actions">
          {/* <div className="dropdown" ref={menuRef}>
            <button onClick={() => setOpen(!open)}>
              <Settings size={14} /> Settings
            </button>

            <div className={`menu ${open ? "open" : ""}`}>
              <button onClick={() => alert("Profile clicked")}>Profile</button>
              <button onClick={() => alert("Preferences clicked")}>
                Preferences
              </button>
              <hr />
              <button
                onClick={() => setTheme(theme === "light" ? "dark" : "light")}
              >
                {theme === "light" ? <Moon size={14} /> : <Sun size={14} />} Theme:{" "}
                {theme === "light" ? "Dark" : "Light"}
              </button>
            </div>
          </div> */}

          {/* <button className="primary-btn">
            <LogOut size={14} /> Sign out
          </button> */}
        </div>
      </header>
    </>
  );
}
