/**
 * üîê RoleRoutes ‚Äî Universal role-based route guard
 *
 * Supports two input styles:
 * 
 * ‚ë† Simple string role (default)
 *    --------------------------------------------
 *    <RoleRoutes role="admin">
 *      <AdminDashboard />
 *    </RoleRoutes>
 *
 *    ‚Ä¢ Only users whose role === "admin" can view.
 *    ‚Ä¢ Unauthenticated users ‚Üí redirected to /sign-in
 *    ‚Ä¢ Others ‚Üí redirected to `redirect` (default: /members/dashboard)
 *
 * 
 * ‚ë° Advanced role object with onTrue/onFalse routes
 *    --------------------------------------------
 *    <RoleRoutes
 *      role={{
 *        admin:  { onTrue: "/admin/home" },
 *        member: { onFalse: "/access-denied" },
 *      }}
 *    >
 *      <ProtectedPage />
 *    </RoleRoutes>
 *
 *    ‚Ä¢ `onTrue`  ‚Üí redirect if user *is* that role.
 *    ‚Ä¢ `onFalse` ‚Üí redirect if user *is not* that role.
 *    ‚Ä¢ Unauthenticated ‚Üí /sign-in.
 *    ‚Ä¢ If no mapping exists ‚Üí fallback redirect used.
 */

import React, { useEffect, useState } from "react";
import { supabase } from "../../../database/supabaseClient";
import { navigate } from "gatsby";

interface RoleRoutesProps {
  children:
    | React.ReactNode
    | ((info: {
        allowed: boolean;
        loading: boolean;
        session: any;
        role: string | null;
      }) => React.ReactNode);
  role?:
    | string
    | Record<
        string,
        {
          onTrue?: string;
          onFalse?: string;
        }
      >;
  redirect?: string;
}

const RoleRoutes: React.FC<RoleRoutesProps> = ({
  children,
  role = "member",
  redirect = "/members/dashboard",
}) => {
  const [session, setSession] = useState<any>(null);
  const [userRole, setUserRole] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);
  const [allowed, setAllowed] = useState(false);

  useEffect(() => {
    const checkAccess = async () => {
      setLoading(true);
      try {
        const { data } = await supabase.auth.getSession();
        const sess = data.session;
        setSession(sess);

        // (1) Not authenticated ‚Üí send to sign-in
        if (!sess?.user) {
          navigate("/sign-in");
          setAllowed(false);
          setUserRole(null);
          return;
        }

        // (2) Extract role
        const r = sess.user.user_metadata?.role ?? "member";
        setUserRole(r);

        // (3) Determine access
        if (typeof role === "string") {
          setAllowed(r === role || (role === "member" && !!r));
        } else {
          const rule = role[r];
          setAllowed(!!rule);
        }
      } catch (err) {
        console.error("RoleRoutes check error:", err);
        setAllowed(false);
      } finally {
        setLoading(false);
      }
    };

    checkAccess();
  }, [role]);

  const renderProps = { allowed, loading, session, role: userRole };

  // Render-function support
  if (typeof children === "function") {
    return <>{(children as any)(renderProps)}</>;
  }

  // Redirect behavior
  useEffect(() => {
    if (loading || !userRole) return;

    if (typeof role === "string") {
      if (!allowed) navigate(redirect);
    } else {
      const rule = role[userRole];
      if (!rule) {
        navigate(redirect);
      } else if (!allowed && rule.onFalse) {
        navigate(rule.onFalse);
      } else if (allowed && rule.onTrue) {
        navigate(rule.onTrue);
      }
    }
  }, [loading, allowed, userRole, role, redirect]);

  if (loading) return null;
  if (!allowed && typeof role === "string") return null;

  return <>{children}</>;
};

export default RoleRoutes;
